<template>
  <div class="week__item">
    <h3 class="week__item-title">{{ getDay }}</h3>
    <p class="week__item-date">{{ getNumber }} {{ getMonth }}</p>
    <div class="week__item-img">
      <img :src="getImg" alt="" />
    </div>
    <p class="week__item-temp">{{ Math.round(day.temp.max) }}°</p>
    <p class="week__item-feels">{{ Math.round(day.feels_like.day) }}°</p>
    <p class="week__item-descr">{{ day.weather[0].description }}</p>
  </div>
</template>

<script>
import {unix} from '@/units'
export default {
  props: ["day"],
  computed: {
    getImg() {
      return `https://openweathermap.org/img/wn/${this.day.weather[0].icon}@2x.png`;
    },
    getDay(){
        return unix(this.day.dt, 'weekday')
    },
    getNumber(){
        return unix(this.day.dt, 'day')
    },
    getMonth(){
        return unix(this.day.dt, 'month')
    }
  },
};
</script>

