<template>
  <section class="content" v-if="weather">
    <div class="container">
      <div class="content__left">
        <p class="content__left-degree">
          {{ Math.round(weather.current.temp) }}°
        </p>
        <p class="content__left-today">Сегодня</p>
        <p class="content__left-time">Время: {{ getTime }}</p>
        <p class="content__left-city">Город: {{ weather.ru }}</p>
        <img class="content__left-icon" :src="getImg" alt="" />
      </div>

      <div class="content__right">
        <img src="@/assets/img/mainBg.svg" alt="" class="content__right-bg" />
        <div class="content__right-item">
          <div class="content__right-item-img">
            <img src="@/assets/img/temp.svg" alt="" />
          </div>
          <p>Температура</p>
          <span>{{ Math.round(weather.current.temp) }}° - ощущается как {{ Math.round(weather.current.feels_like)}}°</span>
        </div>
        <div class="content__right-item">
          <div class="content__right-item-img">
            <img src="@/assets/img/presuure.svg" alt="" />
          </div>
          <p>Давление </p>
          <span>{{ getPressure }} мм ртутного столба - нормальное</span>
        </div>
        <div class="content__right-item">
          <div class="content__right-item-img">
            <img src="@/assets/img/osadki.svg" alt="" />
          </div>
          <p>Вероятность осадков {{ weather.current.humidity }} %</p>
        </div>
        <div class="content__right-item">
          <div class="content__right-item-img">
            <img src="@/assets/img/wind.svg" alt="" />
          </div>
          <p>Ветер</p>
          <span>{{ weather.current.wind_speed }} м/с юго-запад - легкий ветер</span>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { mapState } from "vuex";
import { getPressure } from '@/units'
export default {
  computed: {
    ...mapState(["weather"]),
    getTime() {
      return new Date().toLocaleString("en-US", {
        timeZone: this.weather.timezone,
        hour12: false,
        timeStyle: "short",
      });
    },
    description() {
      return this.weather.current.weather[0].main;
    },
    getImg() {
      if (this.weather) {
        return `https://openweathermap.org/img/wn/${this.weather.current.weather[0].icon}@2x.png`;
      }
    },
    getPressure(){
      return getPressure(this.weather.current.pressure)
    }
  },
};
</script>

<style lang="scss" scoped>
</style>